
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body {
  margin: 100px;
  font-size: 20px;
}
textarea {
  height: 12em;
  width: 35em;
  font-size: 16px;
}
button {
  padding: 5px;
  margin: 10px;
  font-size: 20px;
}
p {
  text-align: left;
}
.ner {
